@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
*{
box-sizing: border-box;
margin: 0;
padding: 0;
font-family: 'Roboto Condensed';
background-color: #ececf9;
color: black;
}

.coin-app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 64px;
  color: white;
}

.coin-search{
  margin-bottom: 64px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.coin-text{
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 250px;
  height: 30px;
  border-radius: 5px;
  border-color: rgb(55, 53, 175);
}

.no-records{
  visibility: hidden;
}

.records{
  margin-top: 2rem;
  color: rgb(211, 25, 25);
  font-weight: bolder;

}
.coin-container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.coin-row{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: start;
            justify-content: start;
    -webkit-align-items:  center;
            align-items:  center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7d7;
    width: 70vw;
}

.coin{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 18rem;
}

.coin h1{
    font-size: 16px;
    width: 9rem;
}
.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
    -webkit-align-self: center;
            align-self: center;
}

.coin-data{
    display: -webkit-flex;
    display: flex;
    text-align: center;
    -webkit-justify-content: start;
            justify-content: start;
        width: 100%;
}

.coin-price{
  
}



.red{
    color: rgb(216, 22, 22);
}

.green{
    color: rgb(29, 185, 29);
}

.coin-data p{
   width: 14vw;
}

.coin-headers{
    display: -webkit-flex;
    display: flex;
}

.coin-headers p{
   width: 13vw;
}

.headers{ 
    padding-left: 4rem;
  
}

